<template>
    <div class="main_disc">
      <div>
        <van-sticky >
        <van-nav-bar
          title="我的优惠"
          left-arrow
          @click-left="onClickLeft"
        />
        </van-sticky>
      </div>
      <div>
        <van-tabs >


          <van-tab title="红包"  >
            <!--红包说明部分-->
            <div class="contenttxt" >
              <span>
              有<span class="color">{{x}}</span>个红包即将到期
            </span>
              <span class="pull-right txtshow">
                <img src="data:image/png;base64," alt="">
                <span class="txthb">红包说明</span>
              </span>
            </div>
                    <!--内容层-->
            <div class="content" v-for="(per,value,index) in arrdata " :key="index">
             <!--背景图-->
             <div class="bg"></div>
              <!--白色框-->
              <div class="contents">
                <!--内部内容层-->
                <div class="textleft ">
                  <div class="pull-left aleft">
                      <span class="s1">￥</span>
                      <span class="s2">{{per.amount}}</span>
                      <!--<span class="s3">.</span>-->
                      <!--<span class="s3">0</span>-->
                    <p class="p1">{{full}}</p>

                  </div>
                  <div  class="pull-left">
                   <h4>{{per.name}}</h4>
                    <p class="p2">{{per.description_map.validity_periods}}</p>
                    <p class="p2">{{per.description_map.phone}}</p>

                  </div>
                  <div class="pull-right sy">
                   <span>{{per.description_map.validity_delta}}</span>
                  </div>

              <div class="clearfix"></div>
                </div>
              </div>

            </div>

            <div class="tohistory">
              <span>
                <router-link :to="{path:'/past'}">
                  查看历史红包 >
                </router-link>
              </span>
            </div>


<!--商家代金券部分-->

          </van-tab >

          <van-tab   title="商家代金券">
            <div class="pull-right djq">
              <img src="data:image/png;base64," alt="">
              <router-link :to="{path:'/supervipnumber'}">商家代金券说明</router-link>
            </div>
            <div class="clearfix"> </div>

            <div class="content_img">
              <img src="data:image/png;base64," alt="">
                <p class="p3">无法使用代金券</p>
                <p class="p4">非客户端或客户端版本过低</p>
              <button class="btn btn-success">下载或升级客户端</button>
            </div>
          </van-tab>
        </van-tabs>
        <router-view></router-view>
      </div>
    </div>
</template>

<script>
  import Vue from "vue"
    export default {
        name: "discounts_zwq",
      data(){
        return{
          x:"",
          full:"",
          arrdata:[],
          isfoot:"true",

        }
      },
      methods:{
          onClickLeft(){
            this.$router.go(-1)
          },
        },
      mounted(){
        Vue.axios.get("https://elm.cangdu.org/promotion/v2/users/1/hongbaos?limit=20&offset=0",
          {}
        ).then((res)=>{
          //红包个数
          this.x=res.data.length
          console.log(res.data)
        this.full=res.data[0].description_map.sum_condition
          this.arrdata=res.data
        }).catch((err)=>{
          console.log(err)
        })
      }
    }
</script>

<style scoped>

  .van-nav-bar__title{
    color: white;
    font-weight:700;
    font-size: 0.18rem;
  }
  .van-nav-bar{
    background: #3190e8;

  }
  .van-icon {
    color: white;
    font-size: 0.25rem;
    margin-left: -100%;
  }


.contenttxt{

  line-height: 0.3rem;
  margin: 3%;
  font-size: 0.1171rem;
  color: #666666;
}
  .color{
    color: #ff5340;
  }
  .txtshow img{
    transform: scale(0.6,0.6 );
  }
  .txthb{
    vertical-align: 0.065rem;
    color: #3190e8;

  }
  .bg{
    width: 3.42rem;
    height: 0.1rem;
    background:url("../../assets/img/1.png") white repeat-x;
    background-size: 0.13rem;
    margin: 0 auto;
    margin-top: 10px;

  }


  .contents{
    background: white;
    width: 91%;
    height: 0.97rem;
    margin: 0 auto;
    margin-top: -1px;
    overflow: hidden;
  }
  .textleft{
    /*background: red;*/
    width: 90%;
    height: 0.55339rem;
    margin: auto;
    margin-top: 5%;
  }
.aleft{
  width: 30%;
  display: inline-block;

  border-right: 1px solid #f5f5f5;
  height: 100%;
}
  .aleft span{
    color: #ff5340;
    /*margin-top: 20%;*/
    display:inline-block;
  }
.aleft:nth-child(1){
  font-size: 0.1757rem;
  font-weight:700;
}

  .s2{
    font-size: 0.3515rem;
    font-weight:400;
    margin-left: -0.1rem;
  }
  .s3{
    margin-left: -0.04rem;
    font-size: 0.1875rem;
    font-weight:700;
  }
.p1{
font-size:0.09375rem;
  color: #999;
  /*background: red;*/
  /*text-align: center;*/
  display: inline-block;
  margin-left: 10px;
  margin-top: 10px;
}
  h4{
    font-size: 0.164rem;
  }
  .p2{
    font-size: 0.0937rem;
    margin: 0px;
    margin-top: 10px;
  }
  .sy{
    color: #ff5340;
    font-size: 0.175781rem;
  }
.tohistory{
  text-align: center;
  margin-top: 0.2rem;
}
  .tohistory a{
    color:#999;
    font-size: 0.11rem;
  }
  .djq{
    margin-top: 0.2rem;
  }
  .djq img{
    width: 0.14rem;
    height: 0.14rem;

  }
  .djq a{
    font-size: 0.1171rem;
    vertical-align: 0.03rem;
    margin-right: 0.2rem;

  }
  .content_img{
    text-align: center;
    margin-top: 100px;
  }
  .content_img img{
    width: 1.463rem;
  }
  .p3{
    font-size: 0.160463rem;
    color: #666;
    margin-top: 0.2rem;
  }
  .p4{
    font-size: 0.1171rem;
    color: #999;
    margin-top: 0.1rem;
  }
  .btn{
    margin-top: 0.1rem;
    font-size: 0.1523rem;
  }
</style>



