          </div>
          <div>
            <img src="data:image/png;base64," alt="">
            <p>邀请QQ好友</p>
          </div>
          <div>
            <img src="data:image/png;base64," alt="">
            <p>面对面邀请</p>
          </div>
      </div>

      <div class="box clearfix">
         <div  class="leftbox left">
           <p class="p1">累计收益</p>
          <p class="num">0</p>
           <span>元</span>
         </div>
      <span class="fenge"></span>
