<template>
  <!--页面根标签-->
    <div class="main_balance">
      <!--余额 nav部分-->
      <div>
        <van-nav-bar
          title="我的余额"
          left-arrow
          @click-left="onClickLeft"
        />
      </div>

      <!--余额部分 第二个盒子-->
      <div class="box_two">
        <!--内部白色盒子-->
        <div class="content">
          <!--内容层 头部文字-->
          <div class="content_header">
            <span class="pull-left">当前余额</span>
            <span class="pull-right" @click="balance_explain">余额说明</span>
            <img class="right header_img" src="data:image/png;base64," alt="">
            <div class="clearfix"></div>
          </div>
          <!--内容层 body-->
          <div class="content_body">
                <span >0.00</span>元
          </div>
          <!--内容层 提现按钮-->
          <div class="content_tx">
              <span>提现</span>
          </div>
        </div>
      </div>

      <div>
        <p class="deal_detail">交易明细</p>
      </div>
      <div class="imgs">
        <img src="data:image/png;base64," alt="">
      <p>暂无明细记录</p>
      </div>

    </div>
</template>

<script>
    export default {
        name: "Balance_zwq",
      methods: {
        onClickLeft() {
          this.$router.go(-1);
        },
        balance_explain(){
          this.$router.push({path:'/explain'})
        }
    }
    }
</script>

<style scoped>
  .van-nav-bar__title{
  color: white;
    font-weight:700;
    font-size: 0.18rem;
  }
  .van-nav-bar{
    background: #3190e8;

  }
  .van-icon {
    color: white;
    font-size: 0.25rem;
    margin-left: -100%;
  }
  .box_two{
    background: #3190e8;
    width: 100%;
    height: 1.7064rem;
    overflow:hidden;
  }

  .content{
    width: 3.6127rem;
    height: 1.564rem;
    background: white;
    margin: 0 auto;
    border-radius: 1%;
    margin-top: 1.5%;
  }
  .pull-left{
    margin: 3% 0% 0% 3%;

  }
  .pull-right{
    margin-top: 3%;
    margin-right: 3%;
    color: #3190e8;
  }
  .content_header span{
    font-size: 0.1286rem;
  }
  .header_img{
    transform: scale(0.6,0.6);
    margin-top: 1.5%;

  }
/*内容层 body部分*/
  .content_body{
    line-height: 0.5rem;

    width: 95%;
    margin: 0 auto;
  }
  .content_body span{
    font-size: 0.4218rem;
    font-weight:400;
  }
  /*提现*/
  .content_tx{
    background: #ccc;
    line-height: 0.46rem;
    width: 95%;
    margin: 0 auto;
    text-align: center;
    color: white;
    margin-top: 3%;
    border-radius:3%;
  }
  .deal_detail{
    margin: 3%;
    color: #999;
    font-size: 0.1406rem;
  }
  .imgs{
    text-align: center;
    width: 100%;
    margin-top:10%;
  }
  .imgs img{
    width: 1.875rem;
    height: 1.1719rem;
  }
  .imgs p{
    color: #666;
    margin-top: 0.2rem;
  }
</style>



