<template>
    <div>
      <van-nav-bar
        title="下载"
        left-arrow
        @click-left="onClickLeft"
      />
      
      <div class="box1">
        <img src="data:image/jpeg;base64," alt="">
        <p>下载饿了么APP</p>
      </div>
      <div class="box2" @click="down">
        下载
      </div>
      <!--弹出部分-->
      <transition name="fade">
      <div class="popup" v-if="reveal">
        <!--圆圈-->
          <div class="popup_box1">
            <span class="popup_s1"></span>
            <span class=" popup_s2"></span>
          </div>
        <p>IOS用户请前往AppStore下载</p>
          <div class="popup_box2" @click="close">
            确认
          </div>
      </div>
      </transition>
    </div>
</template>

<script>



    export default {
        name: "Download_zwq",
      data(){
          return{
            reveal:false
          }
      },
      methods:{
        onClickLeft(){
          this.$router.go(-1)
        },
        //点击下载按钮后
        down(){
         this.reveal=true
        },
        close(){
          this.reveal=false
        }
      },

    }
</script>

<style scoped>
  .van-nav-bar__title{
    color: white;
    font-weight:700;
    font-size: 0.18rem;
  }
  .van-nav-bar{
    background: #3190e8;

  }
  .van-icon {
    color: white;
    font-size: 0.25rem;
    margin-left: -100%;
  }
  .box1{
    width: 100%;
    text-align: center;
    margin-top: 7%;
  }
  .box1 img{
    border-radius: 10%;
  }
  .box1 p{
    font-size: 0.18rem;
    margin-top: 2%;
    color: #666;
  }
  .box2{
    width: 90%;
    background: #4cd964;
   line-height: 0.4rem;
    margin: 0 auto;
    text-align: center;
    margin-top: 6%;
    border-radius: 0.05rem;
    color: white;
  }
  /*弹出部分*/
  .popup{
    top: 28%;
   left: 10%;
    width: 80%;
    background: white;
    /*height: 100px;*/
    overflow: hidden;
    position: absolute;

  }
  .popup p{
    text-align: center;
    margin-top: 5%;
    font-size: 0.16rem;
    color: #333;
  }
  .popup_box1{
    border: 3px solid #f8cb86;
    border-radius: 50%;
       width: 0.8rem;
      height: 0.8rem;
    margin: 0 auto;
    margin-top: 5%;
  }
  .popup_s1{
    display: inline-block;
    background: #f8cb86;
    width: 0.03rem;
    height: 0.4rem;
    margin-left: 48%;
    margin-top: 10%;
  }
  .popup_s2{
    display: inline-block;
    background: #f8cb86;
    width: 0.03rem;
    height: 0.05rem;
    margin-left: 48%;
    border-radius: 50%;
    margin-bottom: 4px;
  }
  .popup_box2{
    background: #4cd964;
    text-align: center;
    line-height: 0.42rem;
    color: white;
    margin-top: 5%  ;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .fade-enter, .fade-leave-to{
    opacity: 0;
  }
  .fade-enter-active, .fade-leave-active{
    animation: one .4s ease-in-out forwards;

  }


  @keyframes one {
    0%{
      transform: scale(0.9,0.9);
    }
    50%{
      transform: scale(1.1,1.1);
    }
    75%{
      transform: scale(0.9,0.9);
    }
    100%{
      transform: scale(1.1,1.1);
    }
  }



</style>



